import React, { useEffect } from "react";
import gsap from "gsap";
import Part from "../part/Part";
import logo from "../../assets/images/logo.png";
import home from "../../assets/images/home.svg";
import calendar from "../../assets/images/calendar.svg";
import chart from "../../assets/images/chart.svg";
import atp from "../../assets/images/atp.svg";
import users from "../../assets/images/users.svg";
import dumbbell from "../../assets/images/dumbbell.svg";
import route from "../../assets/images/route.svg";
import setting from "../../assets/images/setting.svg";
import notification from "../../assets/images/notification.svg"
import "./Menu.css";


const Menu = () => {

    useEffect(() => {
        const handleTransition = () => {
            gsap.to(".menu-wrapper", {
                duration: 0.5,
                ease: "power1.out",
                transform: "none"
            });
        };
        handleTransition();
    }, []);

    return (
        <aside className="menu-wrapper">
            <img className="logo" src={logo} alt="" />
            <nav>
                <Part icon={home} title="" />
                <Part icon={calendar} title="" />
                <Part icon={chart} title="" />
                <Part icon={atp} title="ATP" />
                <Part icon={users} title=" " />
            </nav>
            <nav>
                <Part icon={dumbbell} title=" " />
                <Part icon={route} title="" />
                <Part icon={setting} title="" />
                <Part icon={notification} title=" " />
            </nav>
        </aside>
    );
};


export default Menu;